/* WeatherApp.css */
.weather-container {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
  background-color: #f0f8ff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 20px auto;
}

h1 {
  color: #333;
  margin-bottom: 20px;
}

form {
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  width: 70%;
  max-width: 250px;
}

button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.weather-info {
  border-top: 1px solid #eee;
  padding-top: 20px;
  margin-top: 20px;
}

.weather-info h2 {
  color: #007bff;
  margin-bottom: 10px;
}

.weather-info p {
  margin: 5px 0;
  color: #555;
}

.weather-info img {
  vertical-align: middle;
  margin-left: 10px;
}